@import url(https://fonts.googleapis.com/css2?family=Varela&display=swap);
a.home-option{color:#1A1A1A;text-decoration:inherit;display:flex}a.home-option svg{padding:2px 5px 0 5px}

.kanever-logo svg{width:280px;height:280px;border-radius:50%;background-color:white}.kanever-logo .st0{fill:#AAAAAA}.kanever-logo .st1{fill:#FFFFFF}.kanever-logo .st2{fill:#E8C52E}

.home{background:#FFD029;height:1080px;display:flex;flex-direction:column;align-items:center}.home .logo{margin-top:40px;margin-bottom:80px}.home h1{color:white;margin-top:100px}.home div:last-child{display:flex;justify-content:space-between;align-self:stretch;margin:0 13%}@media (max-width: 480px){.home h1{margin-top:70px}.home .logo{margin-top:25px;margin-bottom:50px}.home div:last-child{flex-direction:column;justify-content:left;grid-gap:20px;gap:20px}}

.polygon svg{padding-top:10px}

.bordered-text-box{color:#939191;max-width:427px;min-height:100px;padding:20px;padding-bottom:0;border:5px solid #FFD029;margin:-5px 0 0 -5px}.bordered-text-box div{border:5px solid #EDEDED;padding:8px 70px 35px 8px;min-height:100px;margin:-5px -50px -20px -5px}.bordered-text-box div p{margin:0}@media (max-width: 705px){.bordered-text-box div{margin-right:0;padding-right:0}}

.about{display:flex;flex-direction:column}.about>div:nth-of-type(1){padding-left:25%}.about .title{display:flex}.about .title>span{align-self:flex-end;height:15px;margin-right:-12px;margin-left:12px;border-left:5px solid #FFD029}.about .title>.polygon{margin-right:5px;margin-left:-5px}.about .main-about{border-left:5px solid #FFD029;border-top:5px solid #FFD029;margin-left:12px}.about .main-about .secondary-text{padding-left:30px;padding-top:30px;max-width:427px;min-height:300px}.about .main-about .secondary-text>p{margin-bottom:0px}.about .knowledge-wrapper{display:flex}.about .knowledge-wrapper>span:nth-of-type(1){border-left:5px solid #FFD029;border-bottom:5px solid #FFD029;min-width:20%;margin-right:-2px;margin-left:12px}.about .knowledge-wrapper>span:nth-of-type(2){border-bottom:5px solid #EDEDED;flex-grow:1}.about .knowledge-wrapper>div{display:flex;align-items:center;margin-bottom:-17px}.about .knowledge-wrapper>div>h3{padding:0 10px}.about>span{border-top:5px solid #EDEDED;max-width:75%;margin-top:-110px;margin-bottom:110px}@media (max-width: 700px){.about>div:nth-of-type(1){padding-left:8%;padding-right:3%}.about .knowledge-wrapper>span:nth-of-type(1){min-width:5%}.about>span{margin-top:-40px;margin-bottom:40px}}

.video{border:4px solid #FFD029}.video iframe{margin-bottom:-30px;margin-right:-30px;padding:10px}@media (min-width: 514px){iframe{width:448px;height:252px}}

.external-videos{display:flex;flex-wrap:wrap;justify-content:center;grid-gap:30px;gap:30px}



.content{font-size:18px}.content-wrap{width:85%;margin:0 auto}

html{scroll-behavior:smooth}body{margin:0;font-family:'Varela', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}h1{font-size:48px;font-weight:400;line-height:1;margin:0}h3{font-size:24px;font-weight:400;line-height:1;margin:0}section{margin-bottom:80px}p{color:#939191}

